export function noop() {
/* do-nothing */ }
/**
 * Downloads a csv copy of a table
 * @param table -
 * @param filename -
 */ export function downloadTable(table, filename = 'download.csv', options) {
    const blob = new Blob([
        table.toCSV(options)
    ]);
    download(filename, 'text/csv', blob);
}
function download(filename, type, data) {
    const dataURI = URL.createObjectURL(data);
    const link = document.createElement('a');
    link.href = dataURI;
    link.type = type;
    link.download = filename;
    link.click();
}
