import { introspect } from '@essex/arquero';
import { useMemo } from 'react';
/**
 * Optionally executes a series of table characterization functions.
 * @param table -
 * @param discover -
 */ export function useTableMetadata(table, existing, discover = false, saveMetadata) {
    return useMemo(()=>{
        if (existing) {
            return existing;
        }
        if (discover) {
            const meta = introspect(table, discover);
            saveMetadata && saveMetadata(meta, table);
            return meta;
        }
    }, [
        table,
        existing,
        discover,
        saveMetadata
    ]);
}
