import { useMemo } from 'react';
export function useColumnCounts(table, visibleColumns) {
    return useMemo(()=>{
        const total = table.numCols();
        const visible = visibleColumns ? visibleColumns.length : total;
        const hidden = total - visible;
        return {
            total,
            visible,
            hidden
        };
    }, [
        table,
        visibleColumns
    ]);
}
